import React from 'react';
import './Navigations.css';
import {Link, NavLink, useNavigate} from "react-router-dom";
import logo from "../../assets/images/Frame.svg"
import {Container, Nav, Navbar, NavDropdown} from "react-bootstrap";
import {useGetCategoriesQuery} from "../../store/shop/shop.api";



const Navigations = () => {

    const navigate = useNavigate();

    const {isLoading, isError, data} = useGetCategoriesQuery();

    return (
        <>
            <Navbar bg="secondary" expand="lg" variant="dark">
                <Container>
                    <Navbar.Brand color="light">
                        <img
                            alt="logo"
                            src={logo}
                            width="25"
                            height="25"
                            className="d-inline-block align-top"
                        />{' '}
                        Shop
                    </Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="me-auto">
                            <NavLink to="/" className="nav-link">Home</NavLink>
                            <NavDropdown title="Categories" id="basic-nav-dropdown" >
                                {data?.map(category => (
                                    <NavDropdown.Item key={Math.random()} onClick={() => navigate('/' + category)} className="bg-danger">
                                        {category.toUpperCase()}
                                    </NavDropdown.Item>
                                ))}
                            </NavDropdown>
                            <NavLink to="/about" className="nav-link">About</NavLink>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </>
    );
};

export default Navigations;