import React from 'react';
import {Product} from "../../models/models";
import './Product-Card.css';
import {Link} from "react-router-dom";
import {useActions} from "../../hooks/actions";
import {useAppSelector} from "../../hooks/redux";
import {Card} from "react-bootstrap";
import CardHeader from "react-bootstrap/CardHeader";


interface Props {
    product: Product,
    showCardContent?: boolean
}

const ProductCard: React.FC<Props> = ({product, showCardContent}) => {

    const {addComments} = useActions();

    const {comments} = useAppSelector(state => state.shop);

    const addNewComment = () => {
        addComments({
            id: Math.random() * 8,
            userId: Math.random() * 8,
            productId: product.id,
            title: 'Something comment....' + product.title
        })
    }

    return (

        <>
            <Card style={{width: showCardContent? '30rem' : '16rem'}} className="m-2">
                <CardHeader className="bg-light">
                    <Card.Img variant="top" width="150" height="200" className="p-4" style={{objectFit: 'contain'}}
                              src={product.image}/>
                </CardHeader>

                {showCardContent &&
                (<>
                    <Card.Body>
                        <Card.Title>{product.title}</Card.Title>
                        <Card.Text>{product.description}</Card.Text>
                    </Card.Body>
                    <Card.Footer className="text-center bg-light">
                        <Card.Text>Price: <strong>{product.price} $</strong></Card.Text>
                    </Card.Footer>
                </>
                )}

                {!showCardContent && (<>
                        <Card.Body>
                            <Card.Title>{product.title}</Card.Title>
                        </Card.Body>
                        <Card.Footer className="d-flex justify-content-center bg-light">
                            <Link to="/about" className="btn bg-warning">Read more...</Link>
                        </Card.Footer>
                    </>
                )}
            </Card>
        </>

    );
};

export default ProductCard;