import React, {useEffect, useState} from 'react';
import {useGetCategoriesQuery, useGetCategoryProductQuery} from "../../store/shop/shop.api";
import {useParams} from "react-router-dom";
import "./Category.css"

const Category = () => {
    const [categories, setCategories] = useState<string[]>([]);
    const {categoryParams} = useParams();

    const {isLoading, isError, data} = useGetCategoriesQuery();
    const {isLoading: s, isError: d, data: c} = useGetCategoryProductQuery(categoryParams!);


    useEffect(() => {
        setCategories(data!);
    }, [data]);


    console.log(s, isError);
    console.log(data, c);
    console.log(d, isLoading);



    return (
        <div>
            <div>
                <ul>
                    {categories?.map(category => {
                        return (
                            <li key={Math.random()}>{category}</li>
                        )
                    })}
                </ul>
            </div>
        </div>
    );
};

export default Category;